import React from "react";
import styles from "./style.module.css";

export default ({ title, content, distributionArea }) => {
    return (
    <div className={styles.list}>
      <div className={styles.title}>{title}</div>
      {content !== null ? (
        <div className={styles.content}>{content}</div>
      ) : null}
      {distributionArea !== null ? (
        <div>
          {
              Object.is(distributionArea.provinceList.length,0)?null:<div className={styles.content}>
              不支持省份
              {distributionArea.provinceList.map(value => value + "、 ")}
            </div>
          }{" "}
          {
              Object.is(distributionArea.cityList.length,0)?null:  <div className={styles.content}>
                      不支持市级
                      {distributionArea.cityList.map(value => value + "、 ")}
                  </div>
          }
            {Object.is(distributionArea.districtList.length,0)?null: <div className={styles.content}>
                不支持区县
                {distributionArea.districtList.map(value => value + "、 ")}
            </div>}

        </div>
      ) : null}
    </div>
  );
};



