import React, { Fragment,PureComponent } from "react";
import { Link } from "@reach/router";
import Loading from "loading/lottie";
import Header from "header/header";
import Item from "./item/serverItem";
import styles from "./style.module.css";

export default class extends PureComponent {
  constructor() {
    super(...arguments);
    this.props.CacheObservable.next({
      thisId: this.props.url,
      historyId: this.props.CacheId,
      requestOptions: {
        method: this.props.requestOptions.method,
        url: this.props.requestOptions.url + this.props.url
      }
    });
  }
  render() {
    return this.props.CacheState ? (
      <div className={styles.list} style={{ height: window.innerHeight }}>
        <Header />
        <div className={styles.header}>服务</div>
        {this.props.CacheState.policyList?this.props.CacheState.policyList.map((value, index) => (
            <Fragment key={index}>
                <Item {...value} />
            </Fragment>
        )):null}
        <div className={styles.return}>
         <Link to={`/test/detail/item/${this.props.url}`}>返回</Link>
        </div>
      </div>
    ) : (
      <Loading stop={Object.is(this.props.CacheState,null)} />
    );
  }
}



